@import "../../../assets/sass/variables";
@import "../../components/slider/slider";

.home {
  .customers {
    padding-top: 20px;
    background: $primary;
    .header {
      color: #fff; } }
  .offer {
    padding-top: 20px;
    background: #F5FBFF; }

  .brand-sentence {
    text-transform: capitalize;
    padding: 20px 0; }
  .why {
    padding-top: 20px;
    background-color: $primary;
    .header {
      color: #fff; } }
  .reasons {
    padding-top: 40px;
    padding-bottom: 40px;
    background-color: $primary;

    article {
      .lnr, h4, p {
        color: white; }
      .lnr {
        font-size: 7em;
        transition: all ease-in-out .2s;
        &:hover {
          color: $accent; } }
      h4 {
        margin-top: 30px;
        font-size: 23px;
        font-weight: 300; } } }
  .info {
    font-size: 12px;
    color: #939c9e; }

  @media (max-width: 991px) {
    .reasons {
      padding-bottom: 20px;
      article {
        margin-bottom: 40px; } } } }
