@import "../../../assets/sass/variables";

.about {
  margin-top: 60px;
  .brand-sentence {
    text-transform: capitalize;
    padding: 20px 0; }
  .brand-image {
    width: 100%;
    height: 500px;
    .overlay {
      height: 100%;
      background: rgba(0,0,0,0.3);
      display: flex;
      flex-direction: column;
      justify-content: center;
      .header {
        font-size: 50px;
        color: #fff;
        font-family: "Open Sans", sans-serif;
        font-weight: 300;
        margin: 0; } } }

  .about_us {
    padding: 40px 0;
    background: $primary;
    color: white;
    img {
      max-width: 100%;
      width: 100%;
      margin-top: 20px; }

    .vision, .mission {
      font-size: 16px;
      p {
        color: white; } }
    .mission {
      margin-top: 30px; } }

  @media(max-width: 768px) {
    .brand-image {
      height: 300px; }
    .overlay {
      .header {
        font-size: 3em; } } } }
